import {mapGetters, mapState} from 'vuex';
    import appAttr from '../../../components/page-component/app-attr/app-attr.vue';

    export default {
        name: 'app-recommended-product',
	    props: {
		    showCat: {
                type: Boolean,
			    default: function() {
			        return true;
			    }
		    },
            showBuyBtn: {
                type: Boolean,
	            default: function() {
	                return true;
	            }
            },
            showGoodsName: {
                type: Boolean,
	            default: function() {
	                return true;
	            }
            },
            showGoodsPrice: {
                type: Boolean,
                default: function() {
                    return true;
                }
            },
            showGoodsTag: {
                type: Boolean,
                default: function() {
                    return true;
                }
            },
            goodsTagPicUrl: {
                type: String,
	            default: function() {
	                return 'https://www.bangdian.net/plugins/diy/assets/images/goods-tag-rx.png';
	            }
            },
            goodsCoverProportion: {
                type: String,
	            default: function() {
	                return '1-1';
	            }
            },
            goodsStyle: {
                type: Number,
	            default: function() {
	                return 1;
	            }
            },
            listStyle: {
                type: Number,
	            default: function() {
	                return 2;
	            }
            },
            list: {
                type: Array,
	            default: function () {
		            return [
		            ];
                }
            },
            fill: {
                type: Number,
	            default: function() {
	                return 2;
	            }
            },
            customizeGoodsTag: {
                type: Boolean,
	            default: function() {
	                return false;
	            }
            },
            catStyle: {
                type: Number,
	            default:function() {
	                return 2;
	            }
            },
            buyBtnText: {
                type: String,
	            default: function() {
	                return '购买放电';
	            }
            },
            buyBtnStyle: {
                type: Number,
	            default: function() {
	                return 1;
	            }
            },
            buyBtn: {
                type: String,
	            default: function() {
	                return 'cart';
	            }
            },
            catList: {
                type: Array,
	            default: function() {
	                return [];
	            }
            }
	    },
	   data() {
            return {
                activeCurrent: 0,
                goods: {},
            }
	   },
		computed: {
			...mapGetters('mallConfig',{
				getVideo: 'getVideo'
			}),
            ...mapState({
                platform: function(state) {
                    return state.gConfig.systemInfo.platform;
                }
            }),
		},
	    methods: {
            change(index) {
                this.activeCurrent = index;
            },
            buyProduct(data) {
                if (data.mch_id === 0) {
                    this.$request({
                        url: this.$api.goods.detail,
                        data: {
                            id: data.id,
                        }
                    }).then(response => {
                        if (response.code === 0) {
                            this.goods = response.data.goods;
                            this.show = Math.random();
                        }
                    })
                } else {
                    this.$request({
                        url: this.$api.mch.goods_detail,
                        data: {
                            mch_id: data.mch_id,
                            id: data.id,
                        }
                    }).then(response => {
                        if (response.code === 0) {
                            this.goods = response.data.goods;
                            this.show = Math.random();
                        }
                    })
                }
            },
            jump(data) {

				if (data.video_url && this.getVideo == 1) {

					uni.navigateTo({
						url: `/pages/goods/video?goods_id=${data.id}`
					});






				} else {
					uni.navigateTo({
						url: data.page_url
					});
				}







            }
	    },
        components: {
            'app-attr': appAttr,
	    }
    }
